  font-size: 1.6rem;
  font-weight: 600;
  letter-spacing: 1.5px;
}



.NoOrder{
  
    font-weight: 700;
    font-size: 2rem;
    display: block;
    color: orangered;
 }

.MainMiddleCard{
    height: 80%;
    width: 80%;
    /* background-color: blue; */
    display: block;
    text-align: center;
    overflow: scroll;
    font-size: 1.5rem; 
}


@media screen and (max-width:750px)
{
    .MainMiddleCard{
        width: 80%;
    }
}

@media screen and (max-width:500px)
{
    .MainMiddleCard{
        width: 80%;
        
    }
}

@media screen and (max-width:400px)
