.SmallCard{
    
    width: 60%;
    background-color: rgb(253, 8, 8);
    border: 2px solid rgb(253, 8, 8);
    border-radius: 15px;
    display: -ms-flexbox;
    display: flex;
    height: 20%;
    -ms-flex-negative: 0;
        flex-shrink: 0;
    padding: 0 1rem;
    font-size: 1.1rem;
    font-size: 600;
    text-transform: capitalize;
    margin:  5px auto;
    cursor: pointer;
    -webkit-transition: all .5s;
    -o-transition: all .5s;
    transition: all .5s;
    -webkit-box-shadow: .5px .5px 4px 1px rgb(44, 43, 43);
            box-shadow: .5px .5px 4px 1px rgb(44, 43, 43);
    color: white;
    font-weight: 600;
    
}

@media screen and (max-width:1100px)
{
    .SmallCard{
        width: 70%;
    }
}

@media screen and (max-width:900px)
{
    .SmallCard{
        width: 85%;
    }
}

@media screen and (max-width:270px)
{
    .SmallCard{
      -ms-flex-direction: column;
          flex-direction: column;
      -ms-flex-pack: center;
          justify-content: center;
      -ms-flex-align: center;
          align-items: center;
    }
    .SmallCard{
        width: 70%;
    }
     
}
@media screen and (max-width:150px)
{
     
    .SmallCard{
        width: 60%;
    }

    .NoOrder{
        font-size: .8rem;
    }

    .Ord{
        font-size: .9rem;
    }
     
}

@media screen and (max-width:110px)
{
     
    .SmallCard{
        width: 60%;
        padding: .2rem .4rem;
    }
     
}
@media screen and (max-width:94px)
{
     
    .SmallCard{
      background-color: transparent;
      color: black;
      border: none;
      -webkit-box-shadow: none;
              box-shadow: none;
      padding-left: 0;
    }
     
}

@media screen and (max-width:85px)
{
     
    .SmallCard{
     text-align: start;
     -ms-flex-pack: start;
         justify-content: start;
     -ms-flex-align: start;
         align-items: flex-start;
     
    }
    .Name{
